





































































































import { Component, Vue } from 'vue-property-decorator';
import createDialog from './component/createDialog.vue';
import { linkList, addCombin, editCombin, linkDetail } from '@api/viewData';
import { arrayFind } from '@utils/util';
import moment from 'moment';

@Component({
  components: { createDialog }
})
export default class Connector extends Vue {
  activeName = '0';
  viewName = '';
  type: any = '';
  typeNmber = '0';
  loading = false;
  showChange = true;
  createFormData: Array<any> = [];
  createFormList: Array<any> = [];
  page = 1;
  limit = 10;
  httpPage = 1;
  httpLimit = 10;
  filePage = 1;
  fileLimit = 10;
  total = 10;
  tableId = '';
  rowData = '';
  dialogTitle = '新增';
  dialogVisible = false;
  editButChange = false;
  createForm: any = {
    name: '',
    subType: '',
    localMenu: '',
    path: '',
    uname: '',
    updateTime: '',
    updataBy: ''
  };
  dataSourceTypes: Array<any> = [
    {
      value: 0,
      label: 'Oracle'
    },
    {
      value: 1,
      label: 'Mysql'
    }
  ];
  webTypes: Array<any> = [
    {
      value: 0,
      label: 'Restful'
    },
    {
      value: 1,
      label: 'WebService'
    }
  ];
  fileTypes: Array<any> = [
    {
      value: 0,
      label: 'Ftp'
    },
    {
      value: 1,
      label: '文件目录'
    }
  ];
  titleBtns() {
    const _this = this;
    return [
      {
        title: '返回',
        onClick: _this.connectButton
      }
    ];
  }
  databaseColumns() {
    const _this = this;
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '序号',
        width: '60'
      },
      {
        prop: 'name',
        label: '连接器名称',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: 'subType',
        label: '数据库类型',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100',
        formatter: _this.dataSubTypeFormatter
      },
      {
        prop: 'path',
        label: '连接地址',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '200'
      },
      {
        prop: 'updateTime',
        label: '修改时间',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: 'updataBy',
        label: '修改人',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: '',
        align: 'center',
        label: '操作',
        width: '100',
        headerAlign: 'center',
        actions: [
          {
            code: 'edit',
            text: '修改',
            onClick: _this.editChange
          },
          {
            code: 'edit',
            text: '详情',
            onClick: _this.detailChange
          }
        ]
      }
    ];
  }
  fileColumns() {
    const _this = this;
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '序号',
        width: '60'
      },
      {
        prop: 'name',
        label: '连接器名称',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: 'subType',
        label: '文档库类型',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100',
        formatter: _this.fileSubTypeFormatter
      },
      {
        prop: 'localMenu',
        label: '根目录',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: 'updateTime',
        label: '修改时间',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100',
        formatter: _this.updateTimeFormatter
      },
      {
        prop: 'updataBy',
        label: '修改人',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: '',
        align: 'center',
        label: '操作',
        width: '100',
        headerAlign: 'center',
        actions: [
          {
            code: 'edit',
            text: '修改',
            onClick: _this.editChange
          },
          {
            code: 'edit',
            text: '详情',
            onClick: _this.detailChange
          }
        ]
      }
    ];
  }
  httpColumns() {
    const _this = this;
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '序号',
        width: '60'
      },
      {
        prop: 'name',
        label: '连接器名称',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: 'subType',
        label: '服务类型',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100',
        formatter: _this.webSubTypeFormatter
      },
      {
        prop: 'path',
        label: '连接地址',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '200'
      },
      {
        prop: 'updateTime',
        label: '修改时间',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: 'updataBy',
        label: '修改人',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: '',
        align: 'center',
        label: '操作',
        width: '100',
        headerAlign: 'center',
        actions: [
          {
            code: 'edit',
            text: '修改',
            onClick: _this.editChange
          },
          {
            code: 'edit',
            text: '详情',
            onClick: _this.detailChange
          }
        ]
      }
    ];
  }
  // 修改
  editChange(row: any) {
    this.showChange = true;
    this.dialogTitle = '修改';
    this.dialogVisible = true;
    this.tableId = row.id;
    linkDetail(row.id).then(({ error, data }) => {
      if (!error) {
        this.editButChange = true;
        this.rowData = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }
  // 详情
  detailChange(row: any) {
    this.dialogTitle = '详情';
    this.dialogVisible = true;
    this.showChange = false;
    this.tableId = row.id;
    linkDetail(row.id).then(({ error, data }) => {
      if (!error) {
        this.editButChange = true;
        this.rowData = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }
  viewSearch() {
    this.handleClick();
  }
  // 页面跳转
  handleClick() {
    let params: object = {};
    if (this.activeName === '0') {
      this.typeNmber = '0';
      params = {
        page: this.page,
        limit: this.limit,
        name: this.viewName,
        type: this.activeName
      };
    } else if (this.activeName === '1') {
      this.typeNmber = '1';
      params = {
        page: this.httpPage,
        limit: this.httpLimit,
        name: this.viewName,
        type: this.activeName
      };
    } else if (this.activeName === '2') {
      this.typeNmber = '2';
      params = {
        page: this.filePage,
        limit: this.fileLimit,
        name: this.viewName,
        type: 2
      };
    }
    this.linkList(params);
  }
  // 数据查询
  linkList(params: object) {
    this.loading = true;
    linkList(params).then(({ data }) => {
      this.createFormData = data.rows;
      this.loading = false;
      this.total = data.total;
    });
  }
  // 分页
  handleSize(val: number) {
    if (this.activeName === '0') {
      this.limit = val;
    } else if (this.activeName === '1') {
      this.httpLimit = val;
    } else if (this.activeName === '2') {
      this.fileLimit = val;
    }
    this.handleClick();
  }
  handleCurrent(val: number) {
    if (this.activeName === '0') {
      this.page = val;
    } else if (this.activeName === '1') {
      this.httpPage = val;
    } else if (this.activeName === '2') {
      this.filePage = val;
    }
    this.handleClick();
  }
  // 连接器管理 - 返回
  connectButton() {
    this.$router.push('/sysSupport/dataService/viewDateManagent');
  }
  dataSubTypeFormatter(
    row: object,
    column: object,
    cellValue: number
    // index: number
  ) {
    return arrayFind(this.dataSourceTypes, 'value', cellValue).label;
  }
  updateTimeFormatter(row) {
    row.updateTime = moment(row.updateTime).format('YYYY-MM-DD HH:mm:ss');
    return row.updateTime;
  }
  webSubTypeFormatter(
    row: object,
    column: object,
    cellValue: number
    // index: number
  ) {
    return arrayFind(this.webTypes, 'value', cellValue).label;
  }
  fileSubTypeFormatter(
    row: object,
    column: object,
    cellValue: number
    // index: number
  ) {
    return arrayFind(this.fileTypes, 'value', cellValue).label;
  }
  // 新增
  addlink() {
    this.dialogTitle = '新增';
    this.showChange = true;
    this.dialogVisible = true;
    this.tableId = '';
  }
  // 新增连接器保存
  saveDialog(params) {
    if (this.tableId) {
      params.id = this.tableId;
      this.editCombin(params);
    } else {
      this.addCombin(params);
    }
    this.editButChange = false;
  }
  // 新增
  addCombin(params: object) {
    addCombin(params).then(({ error }) => {
      if (!error) {
        this.$message.success('新增成功！');
        this.dialogVisible = false;
        this.handleClick();
      } else {
        this.$message.error('新增失败：' + error.message);
      }
    });
  }
  // 修改
  editCombin(params: object) {
    editCombin(params).then(({ error }) => {
      if (!error) {
        this.$message.success('修改成功！');
        this.dialogVisible = false;
        this.handleClick();
      } else {
        this.$message.error(error.message);
      }
      this.tableId = '';
    });
  }
  // 取消
  cancelDialog() {
    this.dialogVisible = false;
    this.editButChange = false;
  }
  // 数据初始化
  created() {
    this.handleClick();
  }
}
