



















































































































































import { Component, Vue, Prop, Emit, Watch } from 'vue-property-decorator';
@Component({
  components: {}
})
export default class CreateDialog extends Vue {
  @Prop(String) dialogTitle: string;
  @Prop({ default: false }) dialogVisible: boolean;
  @Prop({ default: false }) bottonShow: boolean;
  @Prop(String) saveText: string;
  @Prop(String) type: string;
  @Prop(String) cancelText: string;
  @Prop(String) dialogWidth: string;
  @Prop(String) rowData: any;
  @Prop({ default: false }) editButChange: boolean;
  @Emit('saveEmit')
  saveCreat() {
    return this.paramsList;
  }
  @Emit('cancelEmit')
  cancelCreat() {}
  @Watch('type', { immediate: true })
  typeChange() {
    this.changButton();
    this.listChange();
  }

  @Watch('editButChange', { immediate: true })
  editBut() {
    if (this.editButChange === true) {
      const row = this.rowData;
      this.editData(row);
      this.changButton();
    }
  }
  statusDialogData: Array<any> = [
    {
      id: 1,
      code: '',
      desc: '',
      isNormal: '',
      addButton: true
    }
  ];
  columnsData: Array<any> = [
    {
      id: 1,
      key: '',
      value: '',
      addButton: true
    }
  ];
  fileVisible = false;
  databaseVisible = false;
  httpVisible = false;
  nameType = '';
  paramsList: any = '';
  subTypeChange = '';
  pathType = '';
  connectorList: Array<any> = [];
  addList: Array<any> = [
    {
      form: {},
      respCodes: [],
      headers: []
    }
  ];
  createForm: any = {
    name: '',
    subType: '',
    path: '',
    uname: '',
    pwd: '',
    // timeOut: '',
    localMenu: '',
    domainPath: '',
    port: '',
    numberDef: '',
    respField: ''
  };
  rules: any = {
    name: [{ required: true, message: '请输入连接器名称', trigger: 'blur' }],
    viewName: [
      { required: true, message: '请输入数据库名称', trigger: 'change' }
    ],
    localMenu: [{ required: true, message: '请输入根目录', trigger: 'blur' }],
    subType: [{ required: true, message: '请选择', trigger: 'change' }],
    path: [{ required: true, message: '请输入连接地址', trigger: 'blur' }],
    uname: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
    pwd: [{ required: true, message: '请输入密码', trigger: 'blur' }],
    domainPath: [
      { required: true, message: '请输入连接域名地址', trigger: 'blur' }
    ],
    port: [{ required: true, message: '请输入端口号', trigger: 'blur' }]
  };
  statusListHead() {
    const _this = this;
    const data: any = this.bottonShow
      ? [
          {
            prop: 'code',
            label: '状态码',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '100',
            editer: {
              type: 'input',
              key: 'code',
              placeholder: '请输入字段code'
            }
          },
          {
            prop: 'desc',
            label: '描述',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '100',
            editer: {
              type: 'input',
              key: 'desc',
              placeholder: '请输入字段code'
            }
          },
          {
            prop: 'isNormal',
            label: '是否异常',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '100',
            editer: {
              type: 'select',
              key: 'isNormal',
              config: {
                props: {
                  placeholder: '请选择字段类型',
                  clearable: true,
                  'collapse-tags': true,
                  filterable: true
                },
                options: () => {
                  return [
                    {
                      text: '正常',
                      value: '0'
                    },
                    {
                      text: '异常',
                      value: '1'
                    }
                  ];
                }
              }
            }
          }
        ]
      : [
          {
            prop: 'code',
            label: '状态码',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '100'
          },
          {
            prop: 'desc',
            label: '描述',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '100'
          },
          {
            prop: 'isNormal',
            label: '是否异常',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '100'
          }
        ];
    // 非详情显示
    if (this.bottonShow) {
      data.unshift({
        prop: '',
        label: '操作',
        align: 'center',
        showOverflowTooltip: true,
        'min-width': '100',
        fixed: 'right',
        render(h: any, { record }: any) {
          if (record.addButton === true) {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.deleteEdit(record)
                  }
                },
                '删除'
              ),
              h(
                'span',
                {
                  style: 'padding: 0 5px'
                },
                '|'
              ),
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.addEdit(record)
                  }
                },
                '添加'
              )
            ]);
          } else {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.deleteEdit(record)
                  }
                },
                '删除'
              )
            ]);
          }
        }
      });
    }
    return data;
  }
  columns() {
    const _this = this;
    const data: any = this.bottonShow
      ? [
          {
            prop: 'key',
            label: '参数key',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '100',
            editer: {
              type: 'input',
              key: 'key',
              placeholder: '请输入字段code'
            }
          },
          {
            prop: 'value',
            label: '参数value',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '100',
            editer: {
              type: 'input',
              key: 'value',
              placeholder: '请输入字段code'
            }
          }
        ]
      : [
          {
            prop: 'key',
            label: '参数key',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '100'
          },
          {
            prop: 'value',
            label: '参数value',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '100'
          }
        ];
    // 非详情显示
    if (this.bottonShow) {
      data.unshift({
        prop: '',
        label: '操作',
        align: 'center',
        showOverflowTooltip: true,
        'min-width': '100',
        fixed: 'right',
        render(h: any, { record }: any) {
          if (record.addButton === true) {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.deleteEditCol(record)
                  }
                },
                '删除'
              ),
              h(
                'span',
                {
                  style: 'padding: 0 5px'
                },
                '|'
              ),
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.addEditCol(record)
                  }
                },
                '添加'
              )
            ]);
          } else {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.deleteEditCol(record)
                  }
                },
                '删除'
              )
            ]);
          }
        }
      });
    }
    // console.log(data);
    return data;
  }
  // 数据库设置
  listChange() {
    this.fileVisible = false;
    this.databaseVisible = false;
    this.httpVisible = false;
    if (this.type === '0') {
      this.subTypeChange = '数据库类型';
      this.nameType = '连接器名称';
      this.pathType = '连接地址';
      this.databaseVisible = true;
      this.connectorList = [
        {
          value: 0,
          text: 'Oracle'
        },
        {
          value: 1,
          text: 'Mysql'
        }
      ];
    } else if (this.type === '1') {
      this.subTypeChange = '连接器类型';
      this.nameType = '连接器名称';
      this.pathType = '服务地址';
      this.httpVisible = true;
      this.connectorList = [
        {
          value: 0,
          text: 'Restful'
        },
        {
          value: 1,
          text: 'WebService'
        }
      ];
    } else if (this.type === '2') {
      this.subTypeChange = '文档库类型';
      this.nameType = '文档库名称';
      this.pathType = '连接地址';
      this.fileVisible = true;
      this.connectorList = [
        {
          value: 0,
          text: 'Ftp'
        },
        {
          value: 1,
          text: '远程目录'
        }
      ];
    }
  }
  // 修改
  editData(row: any) {
    this.createForm.name = row.name;
    this.createForm.subType = row.subType;
    this.createForm.localMenu = row.localMenu;
    this.createForm.path = row.path;
    this.createForm.uname = row.uname;
    this.createForm.pwd = row.pwd;
    // this.createForm.timeOut = row.timeOut;
    this.createForm.domainPath = row.domainPath;
    this.createForm.port = row.port;
    this.createForm.numberDef = row.numberDef;
    this.createForm.respField = row.respField;
    if (this.type === '1') {
      if (row.respCodes.length > 0) {
        this.statusDialogData = row.respCodes;
      } else {
        this.statusDialogData = [
          {
            id: 1,
            code: '',
            desc: '',
            isNormal: '',
            addButton: true
          }
        ];
      }
      if (row.headers.length > 0) {
        this.columnsData = row.headers;
      } else {
        this.columnsData = [
          {
            id: 1,
            key: '',
            value: '',
            addButton: true
          }
        ];
      }
    }
  }
  // 保存校验
  saveData(createForm: any) {
    const form: any = this.$refs[createForm];
    form.validate((valid: boolean) => {
      if (valid) {
        this.addList.forEach((item) => {
          item.form = this.createForm;
          item.respCodes = this.statusDialogData;
          item.headers = this.columnsData;
        });
        let params: any = {};
        if (this.type === '0') {
          params = {
            name: this.createForm.name,
            type: 0,
            subType: this.createForm.subType,
            path: this.createForm.path,
            // viewName: this.createForm.viewName,
            uname: this.createForm.uname,
            pwd: this.createForm.pwd
            // timeOut: this.createForm.timeOut
            // numberDef: this.createForm.numberDef
          };
        } else if (this.type === '1') {
          params = {
            name: this.createForm.name,
            type: 1,
            subType: this.createForm.subType,
            path: this.createForm.path,
            respField: this.createForm.respField
          };
          if (
            this.statusDialogData.length > 0 &&
            this.statusDialogData[0].code
          ) {
            params.respCodes = this.statusDialogData;
          } else {
            params.respCodes = [];
          }
          if (this.columnsData.length > 0 && this.columnsData[0].key) {
            params.headers = this.columnsData;
          } else {
            params.headers = [];
          }
        } else if (this.type === '2') {
          params = {
            name: this.createForm.name,
            type: 2,
            subType: this.createForm.subType,
            path: this.createForm.path,
            localMenu: this.createForm.localMenu,
            // domainPath: this.createForm.domainPath,
            uname: this.createForm.uname,
            pwd: this.createForm.pwd,
            port: this.createForm.port
          };
        }
        this.paramsList = params;
        this.saveCreat();
        this.cancelData();
      } else {
        return false;
      }
    });
  }
  // 取消校验
  cancelData() {
    const form: any = this.$refs['createForm'];
    form.resetFields();
    this.cancelCreat();
    this.createForm.name = '';
    this.createForm.subType = '';
    this.createForm.localMenu = '';
    this.createForm.path = '';
    this.createForm.uname = '';
    this.createForm.pwd = '';
    // this.createForm.timeOut = '';
    this.createForm.domainPath = '';
    this.createForm.port = '';
    this.createForm.numberDef = '';
    this.createForm.respField = '';
    this.statusDialogData = [
      {
        id: 1,
        code: '',
        desc: '',
        isNormal: '',
        addButton: true
      }
    ];
    this.columnsData = [
      {
        id: 1,
        key: '',
        value: '',
        addButton: true
      }
    ];
  }
  // 删除
  deleteEditCol(row: any) {
    if (this.columnsData.length > 1) {
      for (let i = 0; i < this.columnsData.length; i++) {
        if (this.columnsData[i].id === row.id) {
          this.columnsData.splice(i, 1);
        }
      }
      const addView = this.columnsData.slice(-1);
      addView.forEach((item) => {
        this.$set(item, 'addButton', true);
      });
    }
  }
  // 添加
  addEditCol(row: any) {
    const addData = Object.assign({}, row);

    for (const i in addData) {
      if (i === 'id') {
        addData[i] = addData[i] + 1;
      } else if (i !== 'edits') {
        addData[i] = '';
      }
    }
    this.columnsData.push(addData);
    if (this.columnsData.length > 1) {
      this.columnsData.forEach((item) => {
        this.$delete(item, 'addButton');
      });
    }
    const addView = this.columnsData.slice(-1);
    addView.forEach((item) => {
      this.$set(item, 'addButton', true);
    });
  }
  changButton() {
    const addView = this.columnsData.slice(-1);
    addView.forEach((item) => {
      this.$set(item, 'addButton', true);
    });
    const addVie = this.statusDialogData.slice(-1);
    addVie.forEach((item) => {
      this.$set(item, 'addButton', true);
    });
  }
  // 添加
  addEdit(row: any) {
    const addData = Object.assign({}, row);
    for (const i in addData) {
      if (i === 'id') {
        addData[i] = addData[i] + 1;
      } else if (i !== 'edits') {
        addData[i] = '';
      }
    }
    this.statusDialogData.push(addData);
    if (this.statusDialogData.length > 1) {
      this.statusDialogData.forEach((item) => {
        this.$delete(item, 'addButton');
      });
    }
    const addView = this.statusDialogData.slice(-1);
    addView.forEach((item) => {
      this.$set(item, 'addButton', true);
    });
  }
  // 删除
  deleteEdit(row: any) {
    if (this.statusDialogData.length > 1) {
      for (let i = 0; i < this.statusDialogData.length; i++) {
        if (this.statusDialogData[i].id === row.id) {
          this.statusDialogData.splice(i, 1);
        }
      }
      const addView = this.statusDialogData.slice(-1);
      addView.forEach((item) => {
        this.$set(item, 'addButton', true);
      });
    }
  }

  // 数据初始化
  created() {
    this.listChange();
  }
}
